// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

body {
	background-color: #D3D3D3;
	color: black;
	margin-bottom:100px;
}

.navbar {
	background-color: black;
	padding-bottom: 40px
}

.navbar-inverse, .nav.navbar-nav li a {
   	color: #E31E19;
}

.sidenav {
	background-color: #f1f1f1;
	height: 100%;
}

footer {
	position: relative;
    bottom: -20px;
    margin-bottom: -100px;
}

#my_centered_buttons { 
	display: flex; 
	justify-content: center; 
}

.copyright-padding {
	padding-right: 20px;
}

.post-body-padding {
	padding: 15px 0 15px 0;
}

.single-post-body-padding {
	padding: 5px 0 15px 0;
}

.red, h1, h2, h3 {
	color: #E31E19;
}

p > iframe {
  width: 100%    !important;
  height: 450px   !important;
}

#map {
    width: 100%;
    height: 400px;
    background-color: grey;
}

@media screen and (min-width: 750px) {
.carousel {
	width: 100%;
    height: 625px;
    padding: 10px 70px;
}
}

@media screen and (min-width: 750px) {
.carousel-inner > .item > img {
    margin: 0 auto;
}
}

@media screen and (max-width: 750px) {
.carousel-inner > .item > img, .carousel-inner > .item > a > img {
	width: 100%;
	margin: auto;
}
}

@media screen and (max-width: 750px) {
img {
	width: 100%;
	margin: auto;
}
}

.table, th, thead { border: 1px solid #000000; 
	text-align: center;
} 

.table-bordered > thead > tr > th,
.table-bordered > tbody > tr > th,
.table-bordered > tfoot > tr > th,
.table-bordered > thead > tr > td,
.table-bordered > tbody > tr > td,
.table-bordered > tfoot > tr > td { 
	border: 1px solid #000000; 
}

.table tbody tr:hover td, .table tbody tr:hover th {
    background-color: #E31E19;
}

.btn-h1-spacing {
	margin-top: 15px;
}

.form-spacing-top, .edit-box-spacing  {
	margin-top: 25px;
}

.post-title-spacing {
	margin: -4px 0px 20px 80px;
}

@media screen and (min-width: 750px) {
.contact-box-spacing {
	margin-top: 80px;
}
}

@media screen and (min-width: 750px) {
.location-box-spacing {
	margin-top: 50px;
}
}

#heading h5, h5, h4, h4 { 
	display: inline; 
}


//Comment System SCSS

	.comment {
		margin-bottom: 45px;
	}

	.author-image {
		width: 50px;
		height: 50px;
		border-radius: 50%;
		float: left;
	}

	.author-name {
		float: left;
		margin-left: 15px; 
	}

	.author-name>h4 {
		margin: 5px 0px;
		
	}

	.author-time {
		font-size: 12px;
		font-style: italic;
		color: #aaa;
	}

	.comment-content {
		clear: both;
		margin-left: 65px;
		font-size: 16px;
		line-height: 1.3em;
	}

	.comments-title {
		margin-bottom:45px;
		margin-top: 10px;
	}

	.comments-title>span {
		margin-right: 15px;
	}
